import React, { useState, useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

export default function Contact(){
  const [sent, setSent] = useState(false)
  const [form, setForm] = useState({ name: '', email: '', message: '' })
  const [focused, setFocused] = useState({ name: false, email: false, message: false })
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const formRef = useRef(null)

  useEffect(() => {
    const ctx = gsap.context(() => {
      if(titleRef.current) {
        gsap.fromTo(titleRef.current,
          { opacity: 0, y: 50, filter: 'blur(10px)' },
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            ease: 'power3.out',
            scrollTrigger: {
              trigger: titleRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      if(formRef.current) {
        gsap.fromTo(formRef.current,
          { opacity: 0, y: 30 },
          {
            opacity: 1,
            y: 0,
            duration: 0.8,
            ease: 'power2.out',
            scrollTrigger: {
              trigger: formRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  function submit(e){
    e.preventDefault()
    // simulate
    setTimeout(() => {
      setSent(true)
      gsap.fromTo('.success-message',
        { opacity: 0, scale: 0.8 },
        { opacity: 1, scale: 1, duration: 0.6, ease: 'back.out(1.7)' }
      )
    }, 600)
  }

  return (
    <section 
      id="contact" 
      ref={sectionRef}
      className="py-24 md:py-32 bg-gradient-to-b from-black via-bg-dark to-black grain-bg relative overflow-hidden"
    >
      {/* Background decorativo */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute bottom-0 left-0 w-96 h-96 bg-red rounded-full blur-3xl"></div>
        <div className="absolute top-0 right-0 w-96 h-96 bg-gold rounded-full blur-3xl"></div>
      </div>

      <div className="container relative z-10">
        <h2 
          ref={titleRef}
          className="text-4xl md:text-6xl lg:text-7xl font-heading text-gold mb-4 reveal"
        >
          Conecte-se com o Legado
        </h2>
        <p className="text-white/60 text-lg mb-12 max-w-2xl">
          Entre em contato e faça parte da história.
        </p>

        <div ref={formRef} className="max-w-2xl reveal">
          {sent ? (
            <div className="success-message glass-strong p-8 rounded-xl border border-gold/30 text-center">
              <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gold/20 flex items-center justify-center">
                <svg className="w-8 h-8 text-gold" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
              </div>
              <h3 className="text-2xl font-heading text-gold mb-2">Mensagem Enviada!</h3>
              <p className="text-white/80">Obrigado pelo contato. Retornaremos em breve.</p>
            </div>
          ) : (
            <form onSubmit={submit} className="space-y-6 glass-strong p-8 rounded-xl border border-gold/10">
              <div className="relative">
                <label className="block mb-2">
                  <span className="text-sm font-medium text-white/80 uppercase tracking-wider">Nome</span>
                </label>
                <input 
                  required 
                  value={form.name} 
                  onChange={e => setForm({...form, name: e.target.value})}
                  onFocus={() => setFocused({...focused, name: true})}
                  onBlur={() => setFocused({...focused, name: false})}
                  className={`w-full p-4 rounded-lg bg-black/50 border-2 transition-all duration-300 text-white placeholder-white/40 ${
                    focused.name ? 'border-gold focus:border-gold' : 'border-white/10 focus:border-gold/50'
                  } focus:outline-none focus:ring-2 focus:ring-gold/20`}
                  placeholder="Seu nome"
                />
              </div>

              <div className="relative">
                <label className="block mb-2">
                  <span className="text-sm font-medium text-white/80 uppercase tracking-wider">Email</span>
                </label>
                <input 
                  type="email" 
                  required 
                  value={form.email} 
                  onChange={e => setForm({...form, email: e.target.value})}
                  onFocus={() => setFocused({...focused, email: true})}
                  onBlur={() => setFocused({...focused, email: false})}
                  className={`w-full p-4 rounded-lg bg-black/50 border-2 transition-all duration-300 text-white placeholder-white/40 ${
                    focused.email ? 'border-gold focus:border-gold' : 'border-white/10 focus:border-gold/50'
                  } focus:outline-none focus:ring-2 focus:ring-gold/20`}
                  placeholder="seu@email.com"
                />
              </div>

              <div className="relative">
                <label className="block mb-2">
                  <span className="text-sm font-medium text-white/80 uppercase tracking-wider">Mensagem</span>
                </label>
                <textarea 
                  required 
                  value={form.message} 
                  onChange={e => setForm({...form, message: e.target.value})}
                  onFocus={() => setFocused({...focused, message: true})}
                  onBlur={() => setFocused({...focused, message: false})}
                  className={`w-full p-4 rounded-lg bg-black/50 border-2 transition-all duration-300 text-white placeholder-white/40 resize-none ${
                    focused.message ? 'border-gold focus:border-gold' : 'border-white/10 focus:border-gold/50'
                  } focus:outline-none focus:ring-2 focus:ring-gold/20`}
                  rows={6}
                  placeholder="Sua mensagem..."
                ></textarea>
              </div>

              <button 
                type="submit"
                className="btn-primary w-full mt-6 magnetic-btn"
                data-magnetic
              >
                Enviar Mensagem
              </button>
            </form>
          )}
        </div>
      </div>
    </section>
  )
}
